<template>
    <div class="second-step">
        <div class="content">
            <div class="table-info">
                <span>评估部门/单位：投行-投资银行部</span>
                <span>评估期间：2015-01-01~2015-12-31</span>
            </div>
            <div class="tab-desc"><label>汇总评估</label><span>总分：</span><span style="margin-right:20px">30</span><span>风险等级：</span>较低</div>
            <table class="tableBox">
            <thead>
            <tr>
                <td v-for="(item,index) in tableHeader">{{item.name}}</td>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,index) in tableBody">
                <td>{{item.name}}</td>
                <td>{{item.busis}}</td>
                <td>{{item. personer}}</td>
                <td>{{item.startTime}}</td>
                <td>{{item.endTime}}</td>
            </tr>
            </tbody>
        </table>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            tableHeader: [
                { name: "部门/单位" },
                { name: "权重%" },
                { name: "指标分值" },
                { name: "评估结果" },
                { name: "说明" },
            ],
            tableBody: [
                {
                name: "A股",
                busis: "零售-运营管理部2019-12-21",
                personer: "zhansan",
                startTime: "2019-06-06",
                endTime: "2019-08-06",
                flag: false
                },
            ],
        }
    },
}
</script>
<style lang="scss" scoped>
    .second-step{
        margin-bottom: 40px;
        .content{
            .table-info{
                font-size: 14px;
                margin: 20px auto 40px;
                span{
                   &:nth-child(2){
                       margin-left: 40px;
                   } 
                }
            }
            .tab-desc {
                color: #fff;
                font-size: 14px;
                height: 40px;
                line-height: 40px;
                background: #019FE8;
                padding-left: 20px;
                span {
                    &:nth-child(2) {
                    margin-left: 40px;
                    }
                }
            }
            .tableBox{
                margin-top: 1px;
            }
        }
    }
</style>


