<template>
    <div class="total-evalue">
        <div class="topTitle">
            <span class="topTitle-span1">洗钱风险评估</span>
            <span class="topTitle-span1">
                <img src="../../../../assets/topTitle1.png" alt />
            </span>
            <span class="topTitle-span1">机构洗钱风险评估（汇总评估）</span>
            <span class="topTitle-span1">
                <img src="../../../../assets/topTitle1.png" alt />
            </span>
            <span class="topTitle-span1">评估结果</span>
        </div>
        <div class="center">
            <div class="step-content">
                <Steps :current="current">
                    <Step :title="title" v-for="(title,index) in stepList" :key="index"></Step>
                </Steps>
            </div>
            <div>
                <component :is="tabview"></component>
            </div>
            <div :class="current==3?'button-content width_1000 ml_60':'button-content'" >
                <Button type="primary" v-show="current != 0 && current != 3">计算</Button>
                <Button type="primary" @click="prev" v-show="current > 0">上一步</Button>
                <Button type="primary" @click="next" v-show="current < 3">下一步</Button>
                <Button type="primary" v-show="current == 3">提交审核</Button>
            </div>
        </div>
    </div>
</template>
<script>
import firstStep from './firstStep'
import secondStep from './secondStep'
import thirdStep from './thirdStep'
export default {
    data(){
        return{
            current:0,
            stepList:['评估基础信息','固有风险评估汇总','控制措施控制有效性评估汇总','评估结果'],
            tabview:'firstStep'
        }
    },
    components:{
        firstStep,
        secondStep,
        thirdStep,
    },
    methods:{
        prev(){
            if(this.current > 0){
                this.current --;
            }
            this.switchComp();
        },
        next () {
            if (this.current == 3) {
                this.current = 0;
            } else {
                this.current += 1;
            }
            this.switchComp();
        },
        switchComp(){
            switch(this.current){
                case 0 :
                    this.tabview = 'firstStep';
                    break;
                case 1 :
                    this.tabview = 'secondStep';
                    break;
                case 2 :
                    this.tabview = 'secondStep';
                    break;
                case 3 :
                    this.tabview = 'thirdStep';
                    break;
            }
        }
    }
}
</script>
<style lang="scss" @scoped>
    .total-evalue{
        .center{
            background: #fff;
            padding: 20px 20px 40px;
            margin-top: 10px;
           .step-content{
               .ivu-steps{
                   line-height: 3;
                   .ivu-steps-head{
                       .ivu-steps-icon.ivu-icon{
                           font-size: 40px;
                       }
                       .ivu-steps-head-inner{
                            width: 40px;
                            height: 40px;
                            line-height: 40px;
                            font-size: 18px;
                       }
                   }
               }
           } 
           .button-content{
               text-align: center;
               button{
                width: 100px;
                height: 40px;
                font-size: 16px;
                margin: 0 8px;
               }
           }
            .width_1000{
                width: 1000px;
            }
            .ml_60{
                margin-left: 60px;
            }
        }
    }
</style>


